.slide-menu {
  -webkit-user-select: none;
  margin-bottom: 20px;
}
.slide-menu ul {
  padding: 0;
  -webkit-transition: height .3s;
}
.slide-menu li {
  background-color: #34495E;
  list-style-type: none;
  font-size: 14px;
  cursor: pointer;
}
.slide-menu li a {
  display: block;
  text-decoration: none;
  color: #e1ffff;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: 36px;
  padding-left: 20px;
}
.slide-menu li a:hover {
  color: #95A5A6;
}
.slide-menu li a.selected {
  color: #3361a4;
  background-color: #ffffff;
}
.slide-menu li a .has-child-icon {
  float: right;
  right: 20px;
  top: 12px;
  font-size: 12px;
}
.slide-menu li.selected > a {
  color: #E74C3C;
}
.slide-menu li.opened a .has-child-icon {
  transform: rotate(90deg);
}
.slide-menu > ul ul {
  border-bottom: 1px solid black;
}
.slide-menu > ul ul li {
  background-color: #2C3E50;
}
.slide-menu > ul ul a {
  padding-left: 40px;
}



